.slider-main {
  position: relative;
  width: 100%;
}

.slider-main__list.swiper-wrapper {
  width: 100%;
  height: 904px;
  overflow: hidden;
}

.slider-main__item {
  display: flex;
  max-width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}

.slider-main__item--1  {
  background-image: url("../img/slider-main_01.jpg");
}

.slider-main__item--2  {
  background-image: url("../img/slider-main_02.jpg");
}

.slider-main__item--3  {
  background-image: url("../img/slider-main_03.jpg");
}

.slider-main__card {
  position: relative;
  display: block;
  width: 346px;
  height: 420px;
  background-color: rgba(255, 255, 255, 0.8);
  text-align: center;
}

.slider-main__info.info-card {
  text-align: center;
  margin-top: 55px;
}

.info-card__link.site-link {
  font-size: 25px;
  line-height: 28px;
  font-variation-settings: "wght" 400, "wdth" 90;
}

.info-card__link.site-link::after {
  position: absolute;
  content: "";
  width: 45%;
  height: 4px;
  left: 50%;
  transform: translateX(-50%);
  bottom: -4px;
  text-decoration: none;
  z-index: 3;
}

.slider-main__card picture {

}

.slider-main__card img {
  padding-top: 23px;
}


