.slider-main__toggle {
  position: absolute;
  width: 37px;
  height: 37px;
  bottom: 20px;
  background-color: rgba(255, 255, 255, 0.8);
  border: none;
  cursor: pointer;
  z-index: 10;
}

.slider-main__toggle--prev {
  right: 223px;
  outline: 1px solid red;
}

.slider-main__toggle--next {
  right: 170px;
  transform: scaleX(-1) translateY(0%);

  outline: 1px solid green;
}

.slider-main__toggle::before {
  position: absolute;
  content: "";
  width: 15px;
  height: 3px;
  background-color: #000000;
  top: 0%;
  left: 50%;
  transform: rotate(45deg) translateX(-50%);
  z-index: 15;
}

.slider-main__toggle::after {
  position: absolute;
  content: "";
  width: 15px;
  height: 3px;
  background-color: #000000;
  top: 0;
  left: 50%;
  transform: rotate(-45deg) translateX(-50%);
  z-index: 15;
}
