.info-card {
  padding-left: 50px;
  padding-right: 50px;
  margin-top: 28px;
}

.info-card p {
  font-family: inherit;
  font-size: 25px;
  line-height: 24px;
  font-variation-settings: "wght" 400, "wdth" 90;
  color: #000000;
  text-align: center;
  margin: 0;
  margin-top: 24px;
}

.info-card span {
  font-size: 42px;
  font-variation-settings: "wght" 400, "wdth" 90;
  color: #ef1993;
}

span.info-card__old-price {
  font-size: 25px;
  color: #808080;
  text-decoration: line-through;
}

.info-card__link {
  display: block;
  margin-top: 28px;
}
