.form-search {
  position: relative;
  display: flex;
  align-items: center;
  border: none;
}

.form-search input[type="search"] {
  visibility: hidden;
  height: 24px;
  padding-left: 10px;
  margin-right: 5px;
  border: 0;
  outline: 0;
  cursor: pointer;
}

.form-search button {
  width: 24px;
  height: 24px;
  background: url("../img/search.svg") transparent;
  background-repeat: no-repeat;
  background-size: 100% auto;
  border: none;
  cursor: pointer;
  padding: 0;
}
